import React , { Component } from 'react'
import Arrow from '../img/arrow.png'
import t1 from '../img/t1.jpg'
import AppFooter from './AppFooter'
import {AnimatedOnScroll} from "react-animated-css-onscroll";


class AppThesis extends Component{
    render(){
        
        const arrow = {
            height : '50px' ,
            marginTop : '70px' ,
            marginLeft : '10px'
        }
        
        return(
            <div className  = 'thesis'>
                <div className = 'col-md-8'>
            <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                <h1>We've been expecting you.Eventually, every company needs advice to design, build and scale their business. Here is our why behind the how.</h1>
            </AnimatedOnScroll>
                </div>
                <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                <img src = {Arrow} style = {arrow}  title="See more" alt = 'img'/>
                </AnimatedOnScroll>
            <div className = "padTop">
            <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                        <div className  = 'row t1'>
            
                            <div className = "col-md-6">
                                <h1 style  ={{ color : "gray" }}>1</h1>
                            </div>
                            <div className = "col-md-6">
                                <p>Belief</p>
                                <h4>Our long-avowed goal is to be the product team of choice for high-growth businesses.</h4>
                                <h5 style = {{ color : "gray" , paddingTop : "20px" }}>To be successful we must keep our promises, build quality products at scale, grant unusual autonomy to our team members, and partner with our clients through thick and thin (though we prefer thick and thicker).</h5>
                            </div>
        
                        </div>
        </AnimatedOnScroll>
        <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                        <div className  = 'row t1'>
                            
                            <div className = "col-md-6">
                                <h1 style  ={{ color : "gray" }}>2</h1>
                            </div>
                            <div className = "col-md-6">
                                <p>Mission</p>


                                <h4>We inspire and empower organizations to build and operate great products.</h4>
                                <h5 style = {{ color : "gray" , paddingTop : "20px" }}>We do this by leading the strategy, design and development process for our clients. It’s about more than just expertise; we bring passion, curiosity and experience to every project.</h5>
                            </div>

                        </div>
</AnimatedOnScroll>
<AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                        <div className  = 'row t1'>
                            
                            <div className = "col-md-6">
                                <h1 style  ={{ color : "gray" }}>3</h1>
                            </div>
                            <div className = "col-md-6">
                                <p>Horizon</p>


                                <h4>We plan for longevity.</h4>
                                <h5 style = {{ color : "gray" , paddingTop : "20px" }}>Things change quickly in this industry and we take a long-term view in all decision making. Today’s best practices might not be tomorrow’s so we test new paths—and ultimately produce better results.</h5>
                            </div>

                        </div>
</AnimatedOnScroll>
<AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                        <div className  = 'row t1'>
                            
                            <div className = "col-md-6">
                                <h1 style  ={{ color : "gray" }}>4</h1>
                            </div>
                            <div className = "col-md-6">
                                <p>Projects</p>

                                <h4>We specialize in building category-defining products and companies, going from 0 → 1.</h4>
                                <h5 style = {{ color : "gray" , paddingTop : "20px" }}>Project selection is one of the most important decisions a business can make. We help our clients understand what to build, how to operate it and how to manage risk along the way.</h5>
                            </div>

                        </div>
</AnimatedOnScroll>
<AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                        <div className  = 'row t1'>
                            
                            <div className = "col-md-6">
                                <h1 style  ={{ color : "gray" }}>5</h1>
                            </div>
                            <div className = "col-md-6">
                                <p>Lifespan</p>

                                <h4>We work on a “Build, Operate, Transfer” model.</h4>
                                <h5 style = {{ color : "gray" , paddingTop : "20px" }}>Each project starts with a shared understanding of the endpoint. We help our clients to plan for the future and empower them to operate their product independently. We train, teach and help put the right team in place.</h5>
                            </div>

                        </div>
</AnimatedOnScroll>
<AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                        <div className  = 'row t1'>
                            
                            <div className = "col-md-6">
                                <h1 style  ={{ color : "gray" }}>6</h1>
                            </div>
                            <div className = "col-md-6">
                                <p>Approach</p>

                                <h4>We are big believers in the power of keeping it simple.</h4>
                                <h5 style = {{ color : "gray" , paddingTop : "20px" }}>And we only take on projects we know we can execute. We’ve built our team with deep expertise in a few specialized areas and we employ top talent.</h5>
                            </div>

                        </div>
</AnimatedOnScroll>
<AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                        <div className  = 'row t1'>
                            
                            <div className = "col-md-6">
                                <h1 style  ={{ color : "gray" }}>7</h1>
                            </div>
                            <div className = "col-md-6">
                                <p>Solutions</p>

                                <h4>Most technology projects fail because people misjudge the variability of their constraints.</h4>
                                <h5 style = {{ color : "gray" , paddingTop : "20px" }}>To build a successful product you need to know the limitations. Constraints are living, breathing things and understanding them is equal parts art and science. We help you prioritize—and see the forest, instead of just the trees.</h5>
                            </div>

                        </div>
</AnimatedOnScroll>
                </div>

<AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                <img src = { t1 }  className = "padTop" style  = {{ width : "100%" }} alt = 'img' />
</AnimatedOnScroll>
                <div className = "text-center padTop">
                    <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                    <h1>How we work</h1>
</AnimatedOnScroll>
                </div>
                <div className = "row padTop">
                    <div className = "col-md-4">
                        <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                        <h2>Build</h2>
                        <p>We help you build better products through definition, research, design, and technology.</p>
</AnimatedOnScroll>
                    </div>
                    <div className = "col-md-4">
                        <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                        <h2>Operate</h2>
                        <p>We help operate the product through analysis, growth strategy, and user acquisition.</p>
                        </AnimatedOnScroll>
                    </div>
                    <div className = "col-md-4">
                        <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                        <h2>Transfer</h2>
                        <p>We help you train, hire or scale internal teams to support the success of your product.</p>
                        </AnimatedOnScroll>
                    </div>
                </div>


                <div className = "row  container" style = {{ marginTop : "400px" }}>
                    <div className = "col-md-4">
                        
                    </div>
                    <div className = "col-md-8">
                        <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                        <h1>Agility, flexibility and a tailored approach to each project.</h1>
                        <p className = "text-secondary">The factors that contribute to the success of a product are different each time, so it’s impossible to point to a single process or approach. Rather, we use an anti-process; we’ve studied error signatures and patterns of failure from hundreds of projects across various industries, sizes and platforms. While we consciously avoid the common pitfalls, we approach each project with fresh perspective and flexibility.</p>
</AnimatedOnScroll>
                    </div>
                </div>


                <div className = "row padTop">
                    <div className = "col-md-6">
                        <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                        <h1>FAQ</h1>
                        </AnimatedOnScroll>
                    </div>
                    <div className = "col-md-6">
                        <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                        <div style = {{ borderTop : "1px solid gray" , borderBottom : "1px solid gray" }} className = "p-5">
                            <h4>How do you work? </h4>
                        </div>
                        <div style = {{ borderTop : "1px solid gray" , borderBottom : "1px solid gray" }} className = "p-5">
                            <h4>Can I scale the team up or down?</h4>
                        </div>
                        <div style = {{ borderTop : "1px solid gray" , borderBottom : "1px solid gray" }} className = "p-5">
                            <h4>What if we need specialized skills, services, or tools?</h4>
                        </div>
                        <div style = {{ borderTop : "1px solid gray" , borderBottom : "1px solid gray" }} className = "p-5">
                            <h4>What happens when the project is complete?</h4>
                        </div>
                        <div style = {{ borderTop : "1px solid gray" , borderBottom : "1px solid gray" }} className = "p-5">
                            <h4>I have an in-house design or technology team. How does Versett work with them?</h4>
                        </div>
                        <div style = {{ borderTop : "1px solid gray" , borderBottom : "1px solid gray" }} className = "p-5">
                            <h4>What if our priorities change?</h4>
                        </div>
                        <div style = {{ borderTop : "1px solid gray" , borderBottom : "1px solid gray" }} className = "p-5">
                            <h4>What industries do you work in?</h4>
                        </div>
                        <div style = {{ borderTop : "1px solid gray" , borderBottom : "1px solid gray" }} className = "p-5">
                            <h4>What is your typical project length?</h4>
                        </div>

                         <div style = {{ borderTop : "1px solid gray" , borderBottom : "1px solid gray" }} className = "p-5">
                            <h4>What types of clients do you take on?</h4>
                        </div>
                        <div style = {{ borderTop : "1px solid gray" , borderBottom : "1px solid gray" }} className = "p-5">
                            <h4>How do you bill for travel?</h4>
                        </div>
                        <div style = {{ borderTop : "1px solid gray" , borderBottom : "1px solid gray" }} className = "p-5">
                            <h4>What about third-party costs?</h4>
                        </div>
</AnimatedOnScroll>
                    </div>

                
                </div>

                <div className = "row padTop">
                    <div className = "col-md-6">
                        <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                        <h4 style = {{ width : "50%" }}>Have a project in mind?
Learn how we can help you.</h4>
                        </AnimatedOnScroll>
                    </div>
                    <div className = "col-md-6">
                            <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                            <h4><span className = "text-secondary">01</span> Design a new product</h4>
                            <h4><span className = "text-secondary">02</span> Improve an existing product</h4>
                            <h4><span className = "text-secondary">03</span> Launch a new company</h4>
                            <h4><span className = "text-secondary">04</span> Product consulting</h4>
                            </AnimatedOnScroll>
                    </div>
                </div>

                           <AppFooter />
            </div>
        )
    }
}
export default AppThesis