import  React, { Component } from 'react'
import demoVideo from './demo.mp4'
import {AnimatedOnScroll} from "react-animated-css-onscroll";

class AppRecentProjects extends Component{
    constructor(){
        super();
        
        this.state = {
            demo : 'https://www.youtube.com/watch?v=zPxZwBAW8dU'
        }
    }
    render(){
        
       console.log(this.state.demoVideo)
        
        return(
            <div className = "recentProjects">
            <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                <div className = "col-md-8">
                    <h5 style = {{ color : "gray" }} >Recent Projects</h5>
                    <h1 className = "mt-3">Every month, millions of people use the products we’ve built.</h1>
                </div>  
            </AnimatedOnScroll>
            <video className='videoTag mt-4' autoPlay loop muted>
                <source src={demoVideo} type='video/mp4' />
            </video>
             
            
            <ul className="nav nav-tabs">
  <li className="nav-item">
    <a className="nav-link active" href="#!">Active</a>
  </li>
  <li className="nav-item">
    <a className="nav-link text-dark" href="#!">Enterprise</a>
  </li>
  <li className="nav-item">
    <a className="nav-link text-dark" href="#!">Start up</a>
  </li>
<li className="nav-item">
    <a className="nav-link text-dark" href="#!">Venture</a>
  </li>
</ul>


            </div>
            
        )
    }
}
export default AppRecentProjects