import React, { Component } from 'react'
import {AnimatedOnScroll} from "react-animated-css-onscroll";

class AppWhatWeDo extends Component{
    render(){
        return(
            <div className="row whatWeDo">
                <div className="col-md-8">
            <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                <p>What We Do</p>
                <h1>Our specialized teams lead the product design and development process for high growth companies.</h1>
            </AnimatedOnScroll>
            
                
                </div>
            
                
                <div className="row whatWe">
                    <div className="col-md-3">
            <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                        <p style = { {color : "gray"} }>01</p>
                        <h3>Design a new product</h3>
                        <p style = {{ color : "gray" , marginTop : "50px" }}>Design and develop an industry leading product</p>
            </AnimatedOnScroll>
                    </div>
                    
                    <div className="col-md-3">
            <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                        <p style = { {color : "gray"} }>02</p>
                        <h3>Improve an existing product</h3>
                        <p style = {{ color : "gray" , marginTop : "50px" }}>Focus, prioritized ,and turn your product into a category leader.</p>
            </AnimatedOnScroll>
                    </div>
                    
                    <div className="col-md-3">
            <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                        <p style = { {color : "gray"} }>03</p>
                        <h3>Launch a new company</h3>
                        <p style = {{ color : "gray" , marginTop : "50px" }}>Take your vision from concept to launch</p>
            </AnimatedOnScroll>
                    </div>
                    
                    <div className="col-md-3">
            <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                        <p style = { {color : "gray"} }>04</p>
                        <h3>Product consulting</h3>
                        <p style = {{ color : "gray" , marginTop : "50px" }}>Accelerate your strategic planning process.</p>
            </AnimatedOnScroll>
                    </div>
                </div>
            
            </div>
            
        )
    }
}
export default AppWhatWeDo