import React , { Component } from 'react'
import AppShowcase from './AppShowcase'
import AppWhatWeDo from './AppWhatWeDo'
import AppRecentProjects from './AppRecentProjects'
import AppSecondProjects from './AppSecondProjects'
import AppClientStory from './AppClientStory'





class AppHome extends Component{
    render(){
        return(
            <div>
                <AppShowcase />
                <AppWhatWeDo />
                <AppRecentProjects />
                <AppSecondProjects />
                <AppClientStory />
               
            </div>
        )
    }
}
export default AppHome