import React , { Component } from 'react'
import {AnimatedOnScroll} from "react-animated-css-onscroll";
class AppMore extends Component{
    render(){
        return(
            <div className = 'more'>
            <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                <h1 className = 'col-md-6'>Design a new product</h1>
                <h1 className = 'col-md-6'>   Improve an existing product</h1>
                <h1 className = 'col-md-6'> Launch a new company</h1>
                <h1 className = 'col-md-6'> Product consulting</h1>
                
                <p className = 'col-md-6 padTop'>Thesis</p>
                <p className = 'col-md-6'>Client Stories</p>
                <p className = 'col-md-6'>Careers</p>
                <p className = 'col-md-6'>Diversity Report</p>
                </AnimatedOnScroll>
            </div>
        )
    }
}

export default AppMore