import React , { Component } from 'react'
import car1 from '../img/car1.jpg'
import AppFooter from './AppFooter'
import {AnimatedOnScroll} from "react-animated-css-onscroll";

class AppCareers extends Component{
    render(){
        return(
             
            <div className = "careers pad">
           <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                <h1>Careers</h1>
                <h1 style = {{ color : "gray" }}>A culture of innovation</h1>
             </AnimatedOnScroll>
            <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                <img src = {car1} alt ="img" className ="car1"/>
            </AnimatedOnScroll>
            
                <div className = "padis">
                    <div >
            <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                        <h4>Versett is designed to be a place for doing meaningful work. Our team is tight-knit and dedicated to building great products. Send us something you’ve made that you’re proud of and we’ll be in touch.</h4>
             </AnimatedOnScroll>
                    </div>
           
                </div>
            
                <div className = "row" style = {{ marginTop : "300px" }}> 
                    <div className = "col-md-4">
            <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                            <h1>Opening Roles</h1>
            </AnimatedOnScroll>
                    </div>
           
                     <div className = "col-md-8 aww">
                             <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
            
                                <h4 style = {{ color : "gray" }}>Design</h4>
                                <h4>Product Designer </h4>
                             </AnimatedOnScroll>
                             <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                                <h4 style = {{ color : "gray" }}>Design</h4>
                                <h4>Senior Product Designer </h4>
                             </AnimatedOnScroll>
                             <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                                <h4 style = {{ color : "gray" }}>Engineering</h4>
                                <h4>Developer - Technical Project Lead</h4>
                             </AnimatedOnScroll>
                             <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                                <h4 style = {{ color : "gray" }}>Engineering</h4>
                                <h4>Developer Front-end</h4>
                             </AnimatedOnScroll>
                    </div>
           
                </div>
            
            
                <div className = "row" style = {{ marginTop : "300px" }}> 
                    <div className = "col-md-6 ta ">
                            <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                            <h5>Have a project in mind? Learn how we can help you.</h5>
                            </AnimatedOnScroll>
                    </div>
                     <div className = "col-md-6 ">
                            <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                                
                                <h4><span style = {{ color : "gray" }}>01</span>  Design a new product</h4>
                            </AnimatedOnScroll>
                            <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                                <h4><span style = {{ color : "gray" }}>02</span>  Improve an existing product</h4>
                            </AnimatedOnScroll>
                            <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                               <h4><span style = {{ color : "gray" }}>03</span>  Launch a new company</h4>
                            </AnimatedOnScroll>
                            <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                                <h4><span style = {{ color : "gray" }}>04</span>  Product consulting</h4>
                            </AnimatedOnScroll>
                    </div>
                </div>
            <AppFooter />
            </div>
        )
    }
}
export default AppCareers