import React , { Component  } from 'react'
import c1 from '../img/c1.jpg'
import i1 from '../img/i1.jpg'
import i2 from '../img/i2.jpg'
import i3 from '../img/i3.jpg'
import AppFooter from './AppFooter'
import {AnimatedOnScroll} from "react-animated-css-onscroll";

class AppClientStory extends Component{
    render(){
        return(
            
            <div id="clientStory">
            <div className = "row " >
                <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                    <div className = "col-md-8 mb-5" >
                        <p style = {{ color : "gray" }}>Client Stories</p>
                        <h1>Designing better experiences is a journey. Here are the stories behind the products.</h1>
                    </div>
                
                </AnimatedOnScroll>
                    <div className = "col-md-8 imgHeight" >
            <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                        <div className='imgg'>
                        <img  src = {c1 } alt = "img" />
                        </div>
            </AnimatedOnScroll>
                    </div>
                
                    <div className = "col-md-4 imgHeight" >
             <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                        <p>Rob Gaspar Mast Hair</p>
                        <h1>"Versett has completely reinvented the way that we work across our organization."</h1>
                        <small>Read Rob's story</small>
              </AnimatedOnScroll>
                    </div>
                
                </div>
            <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                <h5 className = "pb-5 pt-5">Latest News</h5>
                <h1 className = "col-md-8">Join over 1,000 subscribers and receive our quarterly newsletter.</h1>
             </AnimatedOnScroll>
                <div >
             <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                <div className = "row" style = {{ margin : "200px 0px 0px 0px" }}>
           
                    <div className = "col-md-4 imgg">
            
                        <img src = {i1} alt = "img" className = "i"/>
                        <br/>
                        <br/>
                        <p>Medium</p>
                        
                        <br/>
                        <p>Read our Diversity and Inclusion Report 2018

</p>
                    </div>
                    <div className = "col-md-4 imgg"> 
                        <img src = {i2} alt = "img" className = "i"/>
                        <br/>
                        <br/>
                        <p>Careers</p>
                        
                        <br/>
                        <p>A culture of innovation. We're hiring all positions in Calgary, New York and Toronto.

Adam and Tam write about the "Things worth talking about at React Conf 2018"
Medium

</p>
                    </div>
                    <div className = "col-md-4 imgg"> 
                        <img src = {i3} alt = "img" className = "i"/>
                        <br/>
                        <br/>
                        <p>Medium</p>
                        
                        <br/>
                        <p>Adam and Tam write about the "Things worth talking about at React Conf 2018"

Offices</p>
                    </div>
            
                </div>
            </AnimatedOnScroll>
                </div>
            
                <br/>
                <br/>
                <br/>
                <br/>
            <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                <div className = 'imgHeight'>
                <p>Offices</p>
                <h1 className = "col-md-8" >We work globally and have offices in Calgary, Toronto, and New York.</h1>
                </div>
            </AnimatedOnScroll>
            <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                <div className = "row mt-5" >
                    <div className = "col-md-8" >
                        <h1 style = { { fontSize : "30px" } }>Have a project in mind? Learn how we can help you.</h1>
                    </div>
                    <div className = "col-md-4 ">
                        <div>
                        <p>Design a new product</p>
                        <p>Improve an existing product</p>
                        <p>Launch a new company</p>
                        <p>Product consulting</p>
                        </div>
                    </div>
                </div>
            </AnimatedOnScroll>
                <AppFooter />
            
                
            
            </div>
            
        )
    }
}
export default AppClientStory