
import React , { Component } from 'react'

import Arrow from '../img/arrow.png'
import {AnimatedOnScroll} from "react-animated-css-onscroll";

class AppShowcase extends Component{
    render(){
        const arrow = {
            height : '50px' ,
            marginTop : '70px' ,
            marginLeft : '10px'
        }
        return(
            <div id= "showcase">
                <div>
                    <div className = "col-md-8 " >
                            <AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
                     <h1 className="text-light">We combine strategy, technology,</h1>
                                <h1 className="text-light">data science and design to solve the</h1>
                                <h1 className="text-light">most complex business challenges.</h1>


                                <button id = "transparentBtn"style = {{ marginTop : '50px' }}>Read Thesis </button>
                </AnimatedOnScroll>


                                
                            <img src = {Arrow} style = {arrow}  title="See more" alt = 'img'/>

                            </div>
                                </div>
            </div>
        )
    }
}
export default AppShowcase